.login{
    position: absolute;
    width: 500px;
    height: 300px;
    left: calc(50% - 250px);
    top: calc(50% - 150px);
    text-align: center;
    border: 1px solid #efefef;
    box-shadow: 2px 2px 2px slategray;
}

.loginForm{
    padding: 10px 10px;
    position: relative;
}

.login>h2{
    margin-top: 10px;
}

.login input {
    margin: 10px 0;
}

.buttonGroup{
    width: 100%;
    /* bottom: 1px; */
}
.buttonGroup button{
    margin: 5px;
}

.anticon-close-circle {
    cursor: pointer;
    color: #ccc;
    transition: color 0.3s;
    font-size: 12px;
  }
  .anticon-close-circle:hover {
    color: #999;
  }
  .anticon-close-circle:active {
    color: #666;
  }