body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login{
    position: absolute;
    width: 500px;
    height: 300px;
    left: calc(50% - 250px);
    top: calc(50% - 150px);
    text-align: center;
    border: 1px solid #efefef;
    box-shadow: 2px 2px 2px slategray;
}

.loginForm{
    padding: 10px 10px;
    position: relative;
}

.login>h2{
    margin-top: 10px;
}

.login input {
    margin: 10px 0;
}

.buttonGroup{
    width: 100%;
    /* bottom: 1px; */
}
.buttonGroup button{
    margin: 5px;
}

.anticon-close-circle {
    cursor: pointer;
    color: #ccc;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
    font-size: 12px;
  }
  .anticon-close-circle:hover {
    color: #999;
  }
  .anticon-close-circle:active {
    color: #666;
  }
.selfTable{
    margin-top: 15px;
}
.totalMoneySetting{
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.selfTable .ant-table-title{
    text-align: center;
}
.selfTable td{
    font-size: 12px;
}
.formWrap{
    padding: 15px 30px;
}
.buttonGroup{
    margin: 15px 0;
}
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header > h1 {
    margin: 0;
}

.inputGroup{
    display: flex;
    align-items: center;
}
.inputGroup > span {
    width: 75px;
}
